"use client";

import React, { useEffect, useState } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import { ChevronLeft, ChevronRight } from 'lucide-react';

const slides = [
  {
    image: 'https://picsum.photos/seed/chai1/1920/1080',
    title: 'Own Your Profitable Chai Café Franchise in India',
    subtitle:
      "Join India's fastest-growing chai franchise with proven success formula - Zero royalty, complete support, and guaranteed territory protection.",
  },
  {
    image: 'https://picsum.photos/seed/cafe2/1920/1080',
    title: 'Be Your Own Boss with Aroma Chai',
    subtitle:
      'A high-ROI business model designed for the modern entrepreneur. Low investment, high returns, and scalable operations.',
  },
  {
    image: 'https://picsum.photos/seed/people3/1920/1080',
    title: 'Revolutionary Chef-Less Kitchen Model',
    subtitle:
      'Operate a full-service café without professional chefs. Our proprietary premix systems ensure consistent quality every single time.',
  },
];

export const Hero: React.FC = () => {
  const [current, setCurrent] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent((prev) => (prev + 1) % slides.length);
    }, 6000);
    return () => clearInterval(timer);
  }, []);

  const next = () => setCurrent((prev) => (prev + 1) % slides.length);
  const prev = () => setCurrent((prev) => (prev - 1 + slides.length) % slides.length);

  return (
    <section className="relative h-screen min-h-[600px] overflow-hidden">
      <AnimatePresence mode="wait">
        <motion.div
          key={current}
          initial={{ opacity: 0, scale: 1.1 }}
          animate={{ opacity: 1, scale: 1 }}
          exit={{ opacity: 0 }}
          transition={{ duration: 1.5, ease: 'easeOut' }}
          className="absolute inset-0"
        >
          <div className="absolute inset-0 gradient-overlay" />
          <img src={slides[current].image} alt="Hero Background" className="w-full h-full object-cover" />
        </motion.div>
      </AnimatePresence>

      <div className="relative z-10 flex flex-col items-center justify-center h-full text-center px-4">
        <motion.h1
          key={`title-${current}`}
          initial={{ y: 30, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.5, duration: 0.8 }}
          className="text-4xl md:text-7xl font-bold text-white max-w-5xl leading-tight mb-6"
        >
          {slides[current].title}
        </motion.h1>

        <motion.p
          key={`sub-${current}`}
          initial={{ y: 30, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.7, duration: 0.8 }}
          className="text-lg md:text-xl text-gray-200 max-w-3xl mb-10"
        >
          {slides[current].subtitle}
        </motion.p>

        <motion.div
          initial={{ scale: 0.9, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 1, duration: 0.5 }}
          className="flex flex-col sm:flex-row gap-4"
        >
          <button className="bg-green-700 hover:bg-green-800 text-white px-10 py-4 rounded-full text-lg font-bold transition-all shadow-xl flex items-center justify-center gap-2 group">
            Contact Support <ChevronRight className="group-hover:translate-x-1 transition-transform" />
          </button>
          <button className="bg-white/10 backdrop-blur-md border border-white/30 text-white hover:bg-white hover:text-green-900 px-10 py-4 rounded-full text-lg font-bold transition-all">
            Download Brochure
          </button>
        </motion.div>
      </div>

      {/* Slide Indicators */}
      <div className="absolute bottom-10 left-1/2 -translate-x-1/2 flex gap-3 z-20">
        {slides.map((_, i) => (
          <button
            key={i}
            onClick={() => setCurrent(i)}
            className={`h-1.5 transition-all rounded-full ${i === current ? 'w-10 bg-white' : 'w-4 bg-white/40'}`}
          />
        ))}
      </div>

      {/* Navigation Buttons */}
      <button
        onClick={prev}
        className="absolute left-6 top-1/2 -translate-y-1/2 p-3 rounded-full bg-black/20 text-white hover:bg-black/50 transition-all z-20 hidden md:block"
      >
        <ChevronLeft size={32} />
      </button>
      <button
        onClick={next}
        className="absolute right-6 top-1/2 -translate-y-1/2 p-3 rounded-full bg-black/20 text-white hover:bg-black/50 transition-all z-20 hidden md:block"
      >
        <ChevronRight size={32} />
      </button>
    </section>
  );
};

